.company_header {
    /* background-color: lightgreen; */
    color: white;
    width: 100%;
    height: 100px;
    color: white;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
  
    -webkit-animation-name: header_slide;
  
            animation-name: header_slide;
    -webkit-animation-duration: .3s;
            animation-duration: .3s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
@-webkit-keyframes header_slide {
    from {-webkit-transform: translateY(-100px);transform: translateY(-100px);}
    to {-webkit-transform: translateY(0);transform: translateY(0);}
  }
@keyframes header_slide {
    from {-webkit-transform: translateY(-100px);transform: translateY(-100px);}
    to {-webkit-transform: translateY(0);transform: translateY(0);}
  }

.company_name {
    cursor: pointer;
    font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
    font-size: 11px;
    letter-spacing: 2px;
    -webkit-transform: scale(1, 1.1);
        -ms-transform: scale(1, 1.1);
            transform: scale(1, 1.1);
}

.company_name:hover {
    color: lightgray;
}
.company_menu {
    cursor: pointer;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    width: 55px;
    -ms-flex-align: center;
        align-items: center;
}
.company_menu:hover .company_menu_logo {
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    -webkit-transform: rotateZ(90deg);
        -ms-transform: rotate(90deg);
            transform: rotateZ(90deg);
}
.company_menu_name {
    font-family: "Soleil W01 Bold", sans-serif;
    font-size: 11px;
    letter-spacing: 1px;
}
/* .company_menu_logo {} */

.company_menu_logo.isDefault {
    content: url('../../menu_line.svg');
}
/* .company_menu_logo.isChanged {
    content: url('../../menu_close.svg');
} */

@media screen and (min-width: 600px) {
    .company_name {
        font-size: 18px;
        letter-spacing: 3px;
    }
    .company_menu_name {
        font-size: 12px;
    }
    .company_menu {
        width: 65px;
    }
}
@media screen and (min-width: 900px) {
    .company_name {
        font-size: 22px;
        letter-spacing: 4px;
    }
}


